module.exports = "<!--\r\n1. 直接将代码写在指令中\r\n2. 通过方法\r\n-->\r\n\r\n\r\n<template>\r\n    <div>\r\n        <div class=\"case-item\">\r\n            <h4>1. 直接将代码写在指令中</h4>\r\n            <el-button @click=\"msg='hi'\"> {{ msg }}</el-button>\r\n        </div>\r\n\r\n        <div class=\"case-item\">\r\n            <h4>2. 通过方法</h4>\r\n            <el-button @click=\"sayHello\"> {{ msg }}</el-button>\r\n        </div>\r\n\r\n    </div>\r\n</template>\r\n\r\n<script lang=\"ts\">\r\n    import {Vue, Component, Watch} from 'vue-property-decorator'\r\n\r\n    @Component\r\n    export default class ArrayChangeDetection extends Vue {\r\n        msg = 'hello'\r\n\r\n        sayHello() {\r\n            this.msg = 'hello'\r\n        }\r\n    }\r\n</script>\r\n\r\n\r\n\r\n";