















































import {Vue, Component, Watch} from 'vue-property-decorator'

@Component
export default class ArrayChangeDetection extends Vue {
    msg = 'hello'

    onButtonClick() {
        setTimeout(() => {
            this.$notify({
                title: 'button被点击',
                message: 'button被点击'
            });
        })
    }

    onSubmit() {
        this.$notify({
            title: 'submit被点击',
            message: 'submit被点击'
        });
    }

    onDivClick() {
        setTimeout(() => {
            this.$notify({
                title: 'Div被点击',
                message: 'Div被点击'
            });
        })
    }

    sayHello() {
        this.msg = 'hello'
    }
}
