module.exports = "<!--\r\n1. stop\r\n2. prevent\r\n3. capture\r\n4. self\r\n5. once\r\n6. passive\r\n.enter\r\n.tab\r\n.delete (捕获“删除”和“退格”键)\r\n.esc\r\n.space\r\n.up\r\n.down\r\n.left\r\n.right\r\n-->\r\n\r\n\r\n<template>\r\n    <div>\r\n        <div class=\"case-item\" @click=\"onDivClick\">\r\n            <h4>1. stop</h4>\r\n            <el-button type=\"primary\" @click.stop=\"onButtonClick\">有stop修饰</el-button>\r\n            <el-button type=\"primary\" @click=\"onButtonClick\">没stop修饰</el-button>\r\n        </div>\r\n\r\n        <div class=\"case-item\">\r\n            <h4>2. prevent</h4>\r\n            <form v-on:submit.prevent=\"onSubmit\" action=\"https://www.baidu.com\">\r\n                <label>有prevent</label>\r\n                <input type=\"submit\"/>\r\n            </form>\r\n            <form v-on:submit=\"onSubmit\" action=\"https://www.baidu.com\">\r\n                <label>无prevent</label>\r\n                <input type=\"submit\"/>\r\n            </form>\r\n        </div>\r\n\r\n        <div class=\"case-item\" @click.capture=\"onDivClick\">\r\n            <h4>3. capture</h4>\r\n            <el-button type=\"primary\" @click=\"onButtonClick\">capture修饰</el-button>\r\n        </div>\r\n\r\n    </div>\r\n</template>\r\n\r\n<script lang=\"ts\">\r\n    import {Vue, Component, Watch} from 'vue-property-decorator'\r\n\r\n    @Component\r\n    export default class ArrayChangeDetection extends Vue {\r\n        msg = 'hello'\r\n\r\n        onButtonClick() {\r\n            setTimeout(() => {\r\n                this.$notify({\r\n                    title: 'button被点击',\r\n                    message: 'button被点击'\r\n                });\r\n            })\r\n        }\r\n\r\n        onSubmit() {\r\n            this.$notify({\r\n                title: 'submit被点击',\r\n                message: 'submit被点击'\r\n            });\r\n        }\r\n\r\n        onDivClick() {\r\n            setTimeout(() => {\r\n                this.$notify({\r\n                    title: 'Div被点击',\r\n                    message: 'Div被点击'\r\n                });\r\n            })\r\n        }\r\n\r\n        sayHello() {\r\n            this.msg = 'hello'\r\n        }\r\n    }\r\n</script>\r\n\r\n\r\n\r\n";